import GridBox from './GridBox'

interface Content {
    photo: string
}

interface Props {
    GridContent: Array<Content>
}


const Grid:React.FC<Props> = (GridContent) => {
    console.log(GridContent.GridContent)
    return (
        <ul>
            {GridContent.GridContent.map((content: Content, index) => {
                <GridBox BoxContent={content}></GridBox>            
            })}
        </ul>
    )
};

export default Grid;